<template>
  <transition name="showUpHideRight">
    <div class="songsheet" ref="songsheet" v-show="isShowSongSheet" @scroll="scrollHandle"  v-if="musicSheetList">
      <header class="header">
        <div class="header_left">
          <i class="back icon-back" @click="hideSongSheet"></i>
          <span class="title">歌单</span>
        </div>
        <div class="header_right">
          <i class="search icon-search"></i>
          <i class="menu icon-list-circle"></i>
        </div>
        <div class="songheaderimg" ref="songheaderimg" :style="{backgroundImage: `url(${musicSheetList.info[0].img_url})`}"></div>
      </header>

      <div class="content">
        <div class="display_area" ref="display_area" :style="{backgroundImage: `url(${musicSheetList.info[0].img_url})`}">
          <div class="songsheetmain">
            <div class="img_warp">
              <img class="display_img" :src="musicSheetList.info[0].img_url" alt="">
            </div>
            <div class="detail">
              <div class="title">{{ musicSheetList.name }}</div>
              <div class="info">
                <img class="avatar" :src="musicSheetList.user.avatar" alt="">
                <span class="name">{{ musicSheetList.user.name }}</span>
                <i class="icon-right"></i>
              </div>
            </div>
          </div>

          <ul class="opear">
            <div class="item">
							<i class="icon-music"></i>
							<span class="disc">收藏</span>
						</div>
						<div class="item">
							<i class="icon-music"></i>
							<span class="disc">评论</span>
						</div>
						<div class="item">
							<i class="icon-music"></i>
							<span class="disc">分享</span>
						</div>
						<div class="item">
							<i class="icon-music"></i>
							<span class="disc">下载</span>
						</div>
          </ul>
        </div>

        <song-list></song-list>
      </div>
    </div>
  </transition>
</template>

<script>
import songList from './song-list'

export default {
  computed: {
    isShowSongSheet() {
      return this.$store.state.audioInfo.isShowSongSheet;
    },
    musicSheetList() {
      return this.$store.state.audioInfo.musicSheetList;
    },
    musicSheetTitle() {
      return this.musicSheetList.name
    }
  },
  methods: {
    hideSongSheet() {
      this.$store.commit({
        type: 'setIsShowSongSheet',
        isShow: false
      });
    },
    // 滚动改变头部背景透明图
    scrollHandle() {
      var displayArea_height = this.$refs.display_area.offsetHeight;
      var songheaderimg_height = this.$refs.songheaderimg.offsetHeight;
      var songsheet_scrolltop = this.$refs.songsheet.scrollTop;

      var opacity = songsheet_scrolltop / (displayArea_height - songheaderimg_height);
      if (opacity > 1) opacity = 1;

      this.$refs.songheaderimg.style.opacity = opacity;
    }
  },
  components: {
    'song-list': songList
  }
}
</script>

<style lang="scss" scoped>
.songsheet {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 15;
  background-color: #fff;
  overflow-x: hidden;

  .header {
    color: #fff;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    left: 0;
    right: 0;
    height: 54px;
    font-size: 25px;
    z-index: 2;
    .back {
      font-size: 22px;
      margin-left: 18px;
    }
    .title {
      font-size: 18px;
      margin-left: 10px;
    }
    .header_left { position: relative; z-index: 1; display: flex; align-items: center; }
    .header_right {
       position: relative; z-index: 1;
      display: flex;
      align-items: center;
    }
    .menu,.search {
      float: left;
      width: 40px;
      text-align: center;
    }
    .menu { font-size: 20px; }
    .songheaderimg {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 54px;
      background-size: 5800%;
      background-position: center;
      opacity: 0;
    }
  }

  .display_area {
    padding: 60px 25px 0 25px;
    height: 265px;
    background-size: 5800%;
    background-position: center;
    color: #fff;
    .songsheetmain {
      display: flex;
      .display_img {
        width: 120px;
        height: 120px;
        margin-right: 18px;
      }
      .title {
        font-size: 16px;
        line-height: 26px;
        height: 52px;
        overflow: hidden;
        margin: 5px 0 26px 0;
      }
      .avatar {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        vertical-align: middle;
        margin-right: 8px;
      }
    }
    .opear {
      display: flex;
      text-align: center;
      margin-top: 20px;
      font-size: 12px;
      .item {
        flex: 1;
        i[class*='icon-'] {
          font-size: 20px;
          display: block;
          margin-bottom: 5px;
        }
      }
    }
  }
}
</style>



