<style lang="scss" scoped>
.musicdetail {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 21;
  height: 100vh;
  background-color: #222;
  overflow: hidden;

  // 模糊的背景图片
  .blurbg {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-size: cover;
    background-position: center;
    opacity: 0.7;
    // -webkit-filter: blur(20px);
    // filter: blur(20px);
  }
  .content {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .header {
    position: relative;
    z-index: 2;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 54px;
    font-size: 25px;
    .back {
      font-size: 22px;
      margin-left: 18px;
    }
    .title {
      font-size: 16px;
      margin-left: 10px;
      span {
        display: block;
      }
      .name {
        line-height: 22px;
      }
      .singer {
        font-size: 14px;
        line-height: 20px;
        color: rgba(255, 255, 255, 0.6);
      }
    }
    .header_left {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
    }
    .header_right {
      position: relative;
      z-index: 1;
      display: flex;
      align-items: center;
    }
    .share {
      font-size: 30px;
      width: 50px;
      text-align: center;
    }
  }

  // cd与歌词显示区域
  .main_wrapper {
    height: calc(100vh - 171px);
    overflow: hidden;
    .cd {
      position: relative;
      height: 100%;
      .swith_line {
        position: relative;
        height: 1px;
        background-image: -webkit-linear-gradient(
          left,
          transparent 10%,
          rgba(255, 255, 255, 0.4) 50%,
          transparent 90%
        );
        .triger {
          position: absolute;
          left: calc(50% - 30px);
          top: -14px;
          width: 90px;
          height: 140px;
          background-image: url("../../../static/image/swith.png");
          background-size: cover;
          transform: rotateZ(-30deg);
          transform-origin: 14px 16px;
          transition: all 0.3s;
          z-index: 3;
          &.playing {
            transform: rotateZ(0);
          }
        }
      }

      .turntable {
        position: absolute;
        width: 45vh;
        height: 45vh;
        top: 70px;
        left: calc(50% - 22.5vh);
        &.turntableLeft-enter,
        &.turntableRight-leave-to {
          left: 100vw;
        }
        &.turntableLeft-leave-to,
        &.turntableRight-enter {
          left: -100vw;
        }
        &.turntableLeft-leave-active,
        &.turntableLeft-enter-active,
        &.turntableRight-leave-active,
        &.turntableRight-enter-active {
          transition: left 0.3s;
        }
        &.playing {
          animation: cdRotate 16s infinite linear;
        }
        @keyframes cdRotate {
          0% {
            transform: rotate(0);
          }
          100% {
            transform: rotate(360deg);
          }
        }
        .cd_mine {
          position: relative;
          z-index: 2;
          display: inline-block;
          width: 44vh;
          height: 44vh;
          background: url("../../../static/image/cd-mine.png");
          background-size: cover;
        }

        img {
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
          width: 30vh;
          height: 30vh;
          border-radius: 50%;
        }
      }

      .musicDo {
        position: absolute;
        display: flex;
        justify-content: space-around;
        font-size: 20px;
        padding-left: 5px;
        width: 70vw;
        margin: auto;
        bottom: 2vh;
        left: 0;
        right: 0;
        color: #fff;
        i {
          width: 40px;
          height: 40px;
          line-height: 40px;
          text-align: center;
        }
      }
    }
    .lyric_wrap {
      position: relative;
      height: 100%;
      .volume_range {
        color: #fff;
        height: 30px;
        display: flex;
        align-items: center;
        .rangeicon {
          line-height: 24px;
          margin-right: 10px;
        }
      }
      .lyric {
        position: relative;
        height: calc(100% - 30px);
        -webkit-overflow-scrolling: touch;
        text-align: center;
        overflow: hidden;
        .dashed_wrap {
          position: absolute;
          z-index: 2;
          top: 50%;
          width: 100%;
          padding: 0 5px;
          transform: translateY(-50%);
          height: 13px;
          display: flex;
          align-items: center;
          color: rgba(255, 255, 255, 0.3);
          font-size: 12px;
          &.hide {
            display: none;
          }
          .arrow {
            border-left: 12px solid rgba(255, 255, 255, 0.3);
            border-top: 7px solid transparent;
            border-bottom: 7px solid transparent;
            height: 0;
          }
          .dashed {
            margin: 0 10px;
            flex: 1;
            height: 0;
            border-top: 1px solid rgba(255, 255, 255, 0.1);
          }
          time {
            margin-top: 1px;
            line-height: 12px;
            vertical-align: middle;
          }
        }
        .lrc {
          position: absolute;
          width: 100%;
          &.animate {
            transition: top 0.4s;
          }

          .musiclrc {
            color: rgba(255, 255, 255, 0.5);
            padding: 7px 0;
            line-height: 1.2;
            &.impactActive {
              color: rgba(255, 255, 255, 0.8);
            }
            &.active {
              color: #fff;
            }
          }
        }
        .nolrc {
          position: absolute;
          width: 100%;
          top: calc(50% - 10px);
          line-height: 20px;
          color: #fff;
        }
      }
    }
  }

  // @L 底部
  .footer {
    height: 117px;
    font-size: 12px;
    color: #fff;

    // @M 按钮组
    .btn_group {
      display: flex;
      height: 93px;
      align-items: center;
      font-size: 26px;
      i {
        text-align: center;
        flex: 1;
        &.playPause {
          font-size: 50px;
        }
      }
    }
  }
}
</style>

<template>
  <transition name="showUpHideRight">
    <div class="musicdetail" v-show="isShowMusicDetail">
      <div class="blurbg" :style="{backgroundImage: `url(${currentMusic.blur_img})`}"></div>
      <div class="content">
        <header class="header">
          <div class="header_left">
            <i class="back icon-back" @click="hideMusicDetail"></i>
            <dfn class="title">
              <span class="name">{{ currentMusic.name }}</span>
              <span class="singer">{{ currentMusic.singer }}</span>
            </dfn>
          </div>
          <div class="header_right">
            <i class="share icon-share"></i>
          </div>
        </header>

        <!-- cd动画与歌词 -->
        <section class="main_wrapper" @click="toggleCdLyric">
          <div class="cd" v-show="showCd">
            <div class="swith_line">
              <div :class="['triger', { playing: isPlaying }]"></div>
            </div>

            <transition :name="cdDuration">
              <div class="turntable playing" :key="currentMusic.id" :style="{animationPlayState: isPlaying ? 'running' : 'paused'}">
                <div class="cd_mine"></div>
                <img :src="currentMusic.img_url" alt="">
              </div>
            </transition>

            <div class="musicDo">
              <i class="icon icon-like"></i>
							<i class="icon icon-download"></i>
							<i class="icon icon-msg"></i>
							<i class="icon icon-list-circle-small"></i>
            </div>
          </div>

          <div class="lyric_wrap" v-show="!showCd">
            <div class="volume_range">
              <rang type="volume" :showCd="showCd">
                <dfn slot="sign" class="sign">
                  <i class="rangeicon icon-volume-medium"></i>
                </dfn>
              </rang>
            </div>

            <div class="lyric" ref="lyric">
              <div :class="['dashed_wrap', {hide: hideDashed}]" @click.stop="startPlayFromTheCurrentTime">
                <div class="arrow"></div>
                <div class="dashed" ref="dashed"></div>
                <time>{{ impactFormatTime }}</time>
              </div>
              <div :class="['lrc', {animate: lrcHaveAnimate}]" ref="lrc" v-if="!nolrc" :style="{top: lrcTop+'px'}" @touchstart="lyricToucheStart" @touchmove.prevent="lyricToucheMove" @touchend="lyricToucheEnd">
                <p
                  :class="['musiclrc', { active: currentLrcIndex === index, impactActive: impactActiveIndex === index }]"
                  v-for="(lrc, index) in currentMusic.lyric"
                  :data-timeid="lrc.timeId"
                  :data-index="index"
                  
                >{{ lrc.text }}</p>
              </div>
              <div class="nolrc" v-if="nolrc">
                没有歌词
              </div>
            </div>
          </div>
        </section>

        <footer class="footer">
          <rang :isShowMusicDetail="isShowMusicDetail" type="progress"></rang>

          <div class="btn_group">
            <i :class="['playType', playTypeIcon ]" @click="switchPlayType"></i>
						<i class="prev icon-prevdetail" @click="playPrevMusic"></i>
						<i :class="['playPause',isPlaying ? 'icon-pause-detail':'icon-playdetail']" @click="playpause"></i>
						<i class="next icon-nextdetail" @click="playNextMusic"></i>
						<i class="menu icon-list-music"></i>
          </div>
        </footer>
      </div>
    </div>
  </transition>
</template>

<script>
import rang from "./rang";
import { format, impact } from "@/assets/js/tools.js";

export default {
  data() {
    return {
      showCd: true,
      lrcTop: 0,
      lrcStartTouchY: undefined,
      lrcHaveAnimate: true,
      lrcOffetTop: 0,
      hideDashed: true,
      lrcTimer: null,
      impactActiveIndex: undefined,
      impactTime: "00:00",
      musiclrcHalfHeight: 0,
      lrcIsTouch: false,
    };
  },
  computed: {
    // 所有的歌词项
    musiclrc() {
      return this.$refs.lrc.children;
    },
    lrcFirstTop() {
      return this.$refs.lyric.offsetHeight / 2 - this.musiclrcHalfHeight;
    },
    lrcLastTop() {
      return (
        this.lrcFirstTop -
        this.musiclrc[this.currentMusic.lyric.length - 1].offsetTop
      );
    },
    // 是否显示音乐详情页
    isShowMusicDetail() {
      return this.$store.state.audioInfo.isShowMusicDetail;
    },
    // 音乐是否播放
    isPlaying() {
      return this.$store.state.audioInfo.playing;
    },
    // 当前音乐
    currentMusic() {
      return this.$store.getters.getCurrentMusic || "";
    },
    // cd的动画方式
    cdDuration() {
      return this.$store.state.audioInfo.cdDuration;
    },
    currentLrcIndex() {
      return this.$store.state.audioInfo.lyricIndex;
    },
    currentTime() {
      return this.$store.state.audioInfo.currentTime;
    },
    // 是否有歌词
    nolrc() {
      return this.currentMusic ? this.currentMusic.lyric.length === 0 : "";
    },
    // touch碰撞到的歌词时间
    impactFormatTime() {
      return format(this.impactTime);
    },
    // 音乐是否开始加载
    musicLoadStart() {
      return this.$store.state.audioInfo.musicLoadStart;
    },
    playTypeIcon() {
      var type = this.$store.state.audioInfo.playType;
      switch (type) {
        case 2:
        return 'icon-music-danqu1'
        case 3:
        return 'icon-music-random'
        default:
        return 'icon-music-shunxu'
      }
    }
  },
  watch: {
    showCd(newVal, oldVal) {
      // 显示歌词情况
      if (!newVal && !this.nolrc) {
        this.$nextTick(function() {
          this.musiclrcHalfHeight = this.musiclrc[0].offsetHeight / 2;
          var height = this.$refs.lyric.offsetHeight;
          var top = this.musiclrc[this.currentLrcIndex].offsetTop;
          this.lrcTop = height / 2 - top - this.musiclrcHalfHeight;
        });
      }
    },
    currentTime(newVal, oldVal) {
      var conformLyrics = [...this.currentMusic.lyric].filter((item, index) => {
        return item.timeId <= Math.floor(newVal);
      });
      var index = conformLyrics.length - 1;
      if (index !== -1) {
        var conformOne = conformLyrics[index];
        this.$store.commit("setLyricIndex", index);

        if (!this.lrcIsTouch) {
          var height = this.$refs.lyric.offsetHeight;
          var top = this.musiclrc[this.currentLrcIndex].offsetTop;
          this.lrcTop = height / 2 - top - this.musiclrcHalfHeight;
        }
      }
    },
    musicLoadStart(newVal) {
      if (newVal) {
        this.clearDashed();
      }
    },
    isShowMusicDetail(newVal,oldVal) {
      if (oldVal) {
        this.clearDashed();
      }
    }
  },
  methods: {
    // 清除虚线
    clearDashed() {
      this.hideDashed = true;
      this.lrcIsTouch = false;
      this.impactActiveIndex = undefined;
    },
    // 隐藏音乐详情页
    hideMusicDetail() {
      this.$store.commit({
        type: "setIsShowMusicDetail",
        isShow: false
      });
    },
    // 音乐的播放与暂停
    playpause() {
      this.$store.dispatch("togglePlay");
    },
    playPrevMusic() {
      this.$store.dispatch("playPrev");
    },
    playNextMusic() {
      this.$store.dispatch("playNext");
    },
    // 切换cd与歌词的显示
    toggleCdLyric() {
      this.showCd = !this.showCd;
      this.clearDashed();
    },
    // 歌词滚动
    lyricToucheStart(e) {
      var touches = e.touches[0];
      this.lrcStartTouchY = touches.pageY;
      this.lrcOffetTop = this.$refs.lrc.offsetTop;
      clearTimeout(this.lrcTimer);
    },
    lyricToucheMove(e) {
      
      var _this = this;
      var touches = e.touches[0];
      var dy = touches.pageY - this.lrcStartTouchY;
      if (dy > 20 || dy < -20) {
        this.lrcHaveAnimate = this.hideDashed = false;
        this.lrcIsTouch = true;
        var changedY = this.lrcOffetTop + dy;

        if (changedY > this.lrcFirstTop) changedY = this.lrcFirstTop;
        if (changedY < this.lrcLastTop) changedY = this.lrcLastTop;
        this.lrcTop = changedY;

        // 虚线与歌词的碰撞检测
        var dashed = this.$refs.dashed;
        [...this.musiclrc].forEach(function(item, index) {
          if (impact(item, dashed)) {
            _this.impactActiveIndex = item.dataset.index * 1;
            _this.impactTime = item.dataset.timeid;
          }
        });
      }
      
    },
    lyricToucheEnd() {
      var _this = this;
      this.lrcHaveAnimate = true;

      this.lrcTimer = setTimeout(function() {
        _this.clearDashed();
      }, 5000);
    },
    // 从当前时间段开始播放
    startPlayFromTheCurrentTime() {
      // 隐藏虚线
      this.clearDashed();

      this.$store.state.audioInfo.audioelement.currentTime = this.impactTime;
      this.$store.commit('playMusic');
    },
    // 切换音乐结束后自动播放的播放类型
    switchPlayType() {
      this.$store.commit('setPlayType');
    }
  },
  components: {
    rang
  },
  destroyed() {
    console.log(1);
  }
};
</script>





