<template>
  <header class="header">
    <i class="icon-menu menu" @click.self="showSideBar"></i>
    <div class="centermenu">
      <router-link :to="{ name: 'mymusic' }">
        <i class="music icon-music"></i>
      </router-link>
      <router-link :to="{name: 'findmusic'}">
        <i class="find icon-wangyi"></i>
      </router-link>
      <router-link :to="{name: 'community'}">
        <i class="community icon-community"></i>
      </router-link>
    </div>
    <!-- 搜索 -->
    <i class="search icon-search"></i>
  </header>
</template>

<script>
export default {
  name: 'main-header',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    // 显示侧边栏用户信息
    showSideBar() {
      this.$store.commit('showBar');
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/global.scss';

.header { display: flex; align-items: center; position: absolute; left: 0; top: 0; right: 0; bottom: 0; height: 50px; z-index: 10; background-color: $primarycolor; justify-content: space-between; color: #fff;
  [class *= "icon-"] { font-size: 26px; font-weight: 400; }
  .menu,.search { padding: 10px 12px; }
  .search { font-size: 24px; }
  .centermenu {
    i { margin: 0 10px; font-size: 24px; color: $link_active; }
    i.find { font-size: 27px; }
    & .active i { color: #fff; }
  }
}
</style>



