<template>
  <div class="menulist">
    <transition name="sideUp">
        <div class="content" v-show="showMenu" :style="{height: menuList.height+'px'}">
          <p class="title">
            {{ menuList.title }}
          </p>
          <type-list v-bind="item" v-for="(item,index) in menuList.content" :key="index"></type-list>
        </div>
    </transition>
    <transition name="fade">
        <div class="mask" @click.stop="hideMenuList" v-show="showMenu"></div>
    </transition>
  </div>
</template>

<script>
import typeList from '@/components/my-music/type-list'

export default {
  computed: {
    showMenu() {
      return this.$store.state.menuList.isShow
    },
    menuList () {
      return this.$store.state.menuList.detail
    }
  },
  components: {
    'type-list': typeList
  },
  methods: {
    hideMenuList() {
      this.$store.commit('hideMenu');
    }
  }
}
</script>

<style lang="scss" scoped>
.menulist {
  font-size: 12px;
  .content {
    background-color: #fff;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    height: auto;
    z-index: 20;
    padding: 25px 0 10px;
    transform: translateY(0);
    .title {
      height: 20px;
      line-height: 20px;
      padding: 0 15px;
    }
    &.sideUp-enter-active,&.sideUp-leave-active {
      transition: transform .3s;
    }
    &.sideUp-enter,&.sideUp-leave-to {
      -webkit-transform: translate3d(0,100%,0);
      transform: translate3d(0,100%,0);
      -webkit-backface-visibility:hidden;
      backface-visibility:hidden;
      -webkit-perspective:1000;
      perspective:1000;
    }
  }
  .mask {
    position: fixed;
    top: 0; 
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(0,0,0,.5);
    z-index: 19;
    opacity: 1;
    &.fade-enter-active,&.fade-leave-active {
      transition: opacity .3s linear;
    }
    &.fade-enter,&.fade-leave-to {
      opacity: 0;
    }
  }

}

</style>



