<template>
  <div class="bottomWrap">
    <div class="bottom_left">
      <img class="music_img" @click="showMusicDetail" :src="currentMusic.img_url">
      <div class="music_detail">
        <p class="music_name">{{ currentMusic.name }}</p>
        <p class="music_singer">{{ currentMusic.singer }}</p>
      </div>
    </div>
    <div class="bottom_right">
      <div class="playpause" @click="playpause"><i :class="isPlaying ? 'icon-pause' : 'icon-play'"></i></div>
      <i class="music_list icon-list-music"></i>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    isPlaying() {
      return this.$store.state.audioInfo.playing
    },
    currentMusic() {
      return this.$store.getters.getCurrentMusic || ''
    }
  },
  methods: {
    // 显示歌曲的详情页
    showMusicDetail() {
      this.$store.commit({
        type: "setIsShowMusicDetail",
        isShow: true
      });
    },
    // 切换播放与暂停
    playpause() {
      this.$store.dispatch('togglePlay');
    }
  }
};
</script>

<style lang="scss">
.bottomWrap {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 50px;
  justify-content: space-between;
  background-color: #fff;
  z-index: 16;
  .bottom_left {
    flex: 1;
    display: flex;
  }
  .music_img {
    width: 40px;
    height: 40px;
    margin: 0 5px;
  }
  .music_singer {
    font-size: 12px;
    color: #9b9c9e;
  }

  .bottom_right {
    width: 81px;
    display: flex;
  }
  .playpause {
    width: 28px;
    height: 28px;
    border: 1px solid #2c2c2c;
    color: #2c2c2c;
    border-radius: 50%;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 15px;
    .icon-play {
      font-size: 14px;
      margin-left: 3px;
    }
  }
  .music_list {
    font-size: 26px;
    color: #2c2c2c;
    line-height: 28px;
  }
}
</style>



