<template>
  <div class="song_list">
    <header class="list_head">
      <div class="play_all" @click="playAllMusic">
        <span class="all_icon">
          <em class="arrow"></em>
        </span>
        <span class="name">播放全部</span>
        <span class="count">(共19首)</span>
      </div>
      <div class="multiselect">
        <i class="icon-list-music"></i>
        <span>多选</span>
      </div>
    </header>

    <div class="song_main">
      <musicSheetItem @click.native="playCurrentMusic(index)" v-for="(item, index) in musicSheetList.info" :key="index" :option="item" :show-menu="showMenu" :height="58">
        <div class="songsign" slot="sheetimg">
          <span v-show="!showVolume(index)" class="songIndex">{{ index+1 }}</span>
          <i v-show="showVolume(index)" class="laba icon-volume-medium"></i>
        </div>
        
        <template>
          <p class="name">{{ item.name }}</p>
          <p class="count">{{ item.singer }}</p>
        </template>
      </musicSheetItem>
    </div>
  </div>
</template>

<script>
import musicSheetItem from '../my-music/music-sheet-item'

export default {
  components: {
    musicSheetItem
  },
  computed: {
    musicSheetList() {
      return this.$store.state.audioInfo.musicSheetList
    },
    // 当前播放音乐的索引
    currentIndex() {
      return this.$store.state.audioInfo.currentIndex
    },
    musicList() {
      return this.$store.state.audioInfo.musicList
    }
  },
  methods: {
    showMenu(name) {
      this.$store.commit({
        type: 'showMenu',
        amount: {
          height: 105,
          title: `${name}`,
          content: [
            {
              name: '删除',
              iconinfo: 'icon-delete',
              count: 'none',
              bgcolor: '#fff'
            }
          ]
        }
      });
    },
    // 播放当前索引的音乐
    playCurrentMusic(index) {
      this.$store.commit('setMusicList', this.musicSheetList.info);
      this.$store.dispatch('playIndexMusic', index);
    },
    // 播放全部
    playAllMusic() {
      this.$store.dispatch('playIndexMusic', 0);
    },
    // 是否显示播放图标（喇叭）
    showVolume(index) {
      return this.musicList[this.currentIndex] === this.musicSheetList.info[index]
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/global.scss';
.song_list {
  .list_head {
    display: flex;
    height: 50px;
    line-height: 50px;
    justify-content: space-between;
    border-bottom: 1px solid $border_1px;
    .play_all { font-size: 0; flex: 1; padding-left: 15px;
      &:active { background-color: $list_active; }
      .all_icon {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 20px;
        height: 20px;
        border: 1px solid #333;
        border-radius: 50%;
      }
      .arrow {
        position: absolute;
        top: calc(50% - 4px);
        left: calc(50% - 3.5px);
        display: inline-block;
        border-left: 7px solid #333;
        border-top: 4px solid transparent;
        border-bottom: 4px solid transparent;
      }
      span { vertical-align: middle; }
      .name { font-size: 16px; margin-left: 15px; }
      .count { color: #838486; font-size: 14px; margin-left: 2px; }
    }
    .multiselect { padding: 0 10px;
      &:active { background-color: $list_active; }
      i {
        font-size: 18px;
        vertical-align: middle;
      }
      span {
        vertical-align: middle;
      }
    }
  }
  
  .song_main {
    .songsign {
      width: 52px;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      
    }
    .songIndex {
      color: #999;
      font-size: 20px;
    }
    .laba {
      color: $primarycolor;
    }
  }
}
</style>




