<template>
  <aside class="sidebar">
    <transition name="fade">
      <div v-show="sidebarIsShow" class="sidebar_mask" @click="hideSideBar"></div>
    </transition>
    
    <transition name="sideRight">
      <div v-show="sidebarIsShow" class="sidebar_container">
        <header class="sidebar_detail">
          <div class="avatar">
            <img src="../../../static/image/userlogo.jpg" alt="">
          </div>
          <div class="user_info">
            <span class="name">完美蛋炒饭</span>
            <img class="isvip" src="../../../static/image/vip.png" alt="">
            <span class="lv">Lv.7</span>
            <span class="qd"><i class="icon-coin"></i> <em>签到</em></span>
          </div>
        </header>

        <section class="content">
          <sideitem iconinfo="icon-message" title="我的信息"></sideitem>
          <sideitem iconinfo="icon-vip" title="VIP会员">
            <dfn class="other">2018.05.10到期</dfn>
          </sideitem>
          <sideitem iconinfo="icon-market" title="商城"></sideitem>
          <sideitem iconinfo="icon-friend" title="我的好友" haveBorT="true"></sideitem>
          <sideitem iconinfo="icon-place" title="附近的人">
            <dfn class="other"><span>早起我一人，叫醒全寝室</span> <em class="cir"></em></dfn>
          </sideitem>
          <sideitem iconinfo="icon-theme" title="个性换肤" haveBorT="true">
            <dfn class="other">官方红</dfn>
          </sideitem>
          <sideitem iconinfo="icon-lock" title="音乐闹钟"></sideitem>
          <sideitem iconinfo="icon-set-time" title="定时播放"></sideitem>
          <sideitem iconinfo="icon-car" title="驾驶模式"></sideitem>
          <sideitem iconinfo="icon-cloud" title="音乐云盘"></sideitem>
        </section>

        <footer class="footer">
          <div class="item nightmode">
            <i class="icon-night"></i>
            <span>夜间模式</span>
          </div>

          <div class="item setting">
            <i class="icon-setting"></i>
            <span>设置</span>
          </div>

          <div class="item exit">
            <i class="icon-exit"></i>
            <span>退出</span>
          </div>
        </footer>
      </div>
    </transition>
  </aside>
</template>

<script>
import sideitem from "./sideitem";

export default {
  props: {
    info: Object
  },
  computed: {
    sidebarIsShow() {
      return this.$store.state.sidebar.sidebarIsShow
    }
  },
  methods: {
    hideSideBar() {
      this.$store.commit('hideBar');
    }
  },
  components: {
    sideitem
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";
.sidebar { position: relative; z-index: 30; }
.sidebar_mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $maskBackground;
  &.fade-enter-active,&.fade-leave-active {
    transition: opacity .4s;
  }
  &.fade-enter,&.fade-leave-to {
    opacity: 0;
  }
}
.sidebar_container {
  overflow-x: hidden;
  position: fixed;
  -webkit-overflow-scrolling: touch;
  top: 0;
  left: 0;
  bottom: 0;
  width: 310px;
  padding-bottom: 50px;
  background-color: #fff;
  &.sideRight-enter,
  &.sideRight-leave-to {
    left: -310px;
  }
  &.sideRight-enter-active,&.sideRight-leave-active {
    transition: left .4s;
  }
  .sidebar_detail {
    overflow: hidden;
    height: 180px;
    background-image: url("http://www.daiwei.org/index/images/img/indeximg.jpg");
    background-size: cover;
    .avatar {
      margin: 62px 0 9px 9px;
      background-color: #fff;
      width: 68px;
      height: 68px;
      border-radius: 50%;
      box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .user_info {
      color: #fff;
      padding: 0 10px;
      height: 26px;
      .name {
        display: inline-block;
        vertical-align: middle;
        line-height: 26px;
        font-size: 16px;
        text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
      }
      .isvip {
        display: inline-block;
        vertical-align: middle;
        width: 14px;
        height: 14px;
      }
      .lv {
        display: inline-block;
        vertical-align: middle;
        width: 34px;
        height: 18px;
        font-size: 12px;
        text-align: center;
        transform: scale(0.9);
        line-height: 17px;
        border-radius: 10px;
        border: 1px solid #fff;
        box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
        text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
      }
      .qd {
        float: right;
        width: 68px;
        text-align: center;
        border: 1px solid #fff;
        height: 26px;
        line-height: 26px;
        vertical-align: middle;
        border-radius: 13px;
        box-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
        text-shadow: 0 0 2px rgba(0, 0, 0, 0.5);
        font-size: 12px;
        i {
          font-size: 14px;
          margin-right: 2px;
        }
      }
    }
  }

  .content {
    .other {
      span {
        display: inline-block;
        vertical-align: middle;
      }
    }
    .cir {
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $primarycolor;
      display: inline-block;
      vertical-align: middle;
    }
  }

  .footer {
    position: fixed;
    bottom: 0;
    width: 310px;
    background-color: #fff;
    height: 48px;
    line-height: 48px;
    box-shadow: 0 -1px 2px rgba(0, 0, 0, 0.1);
    .item {
      width: 33.3333333%;
      float: left;
      &:active {
        background-color: $list_active;
      }
      i { margin-right: 5px; }
      i,span { vertical-align: middle; }
      &.nightmode { padding-left: 12px; }
      &.setting, &.exit { padding-left: 24px; }
      .icon-setting { font-size: 18px; }
    }
  }
}
</style>



