import { format } from '@/assets/js/tools.js'

export default {
  state: {
    // audio元素
    audioelement: '',
    // 全部音乐信息 包括歌单等等
    musicAllList: [],
    // 音乐播放列表信息
    musicList: [],
    // 歌单列表页是否显示
    isShowSongSheet: false,
    // 音乐播放器的歌单类表数据
    musicSheetList: '',
    // 歌曲cd页是否显示
    isShowMusicDetail: false,
    // 当前播放音乐的index
    currentIndex: 0,
    // 是否在播放
    playing: false,
    // 音乐是否在加载
    musicLoadStart: false,
    // 音乐的总时长
    musicDuration: 0,
    // 当前播放时间
    currentTime: 0,
    // 当前音乐是否可以播放
    canPlay: false,
    // 上一首/下一首cd的动画，左侧进入还是右侧进入
    cdDuration: 'turntableLeft',
    // 是否是音乐timeupdate
    isMusicTimeUpdate: false,
    // 音乐声音
    volume: 0.9,
    // 歌词信息索引
    lyricIndex: 0,
    // 播放类型 1是顺序播放 2是单曲循环 3是随机播放
    playType: 1,
  },
  getters: {
    // 当前音乐详细信息
    getCurrentMusic: state => state.musicList[state.currentIndex],
    // 获取当前音乐的总时长
    getMusicDuration: state => format(state.musicDuration),
    // 获取音乐的播放时长
    getCurrentTime: state => format(state.currentTime),
    // 获取当前音乐播放的进度
    getPercent: state => state.currentTime / state.musicDuration,
  },
  mutations: {
    // 插入所有音乐信息
    setMusicAllList(state, obj) {
      state.musicAllList = obj
      state.musicList = obj.all
    },
    // 设置音乐播放列表信息
    setMusicList(state, list) {
      state.musicList = list
    },
    // 获取音乐元素 DOM
    setAudioElement(state, ele) {
      state.audioelement = ele
    },
    // 设置是否显示歌单信息
    setIsShowSongSheet(state, obj) {
      state.isShowSongSheet = obj.isShow;
    },
    // 设置音乐播放器的歌单列表数据
    setMusicSheetList(state, obj) {
      state.musicSheetList = obj.data;
    },
    // 是否显示歌曲cd页面
    setIsShowMusicDetail(state, obj) {
      state.isShowMusicDetail = obj.isShow;
    },
    // 设置音乐是否在加载
    setMusicLoadStart(state, obj) {
      state.musicLoadStart = obj.isLoad;
    },
    // 设置当前播放时间
    setCurrentTime(state, obj) {
      state.currentTime = obj.time;
    },
    // 设置当前音乐的总时长与是否可以播放
    setMusicDuration(state, obj) {
      state.musicDuration = obj.duration;
    },
    // 设置当前音乐是否加载完
    setMusicCanPlay(state, obj) {
      state.canPlay = obj.canPlay;
    },
    // 设置上一首/下一首cd的动画，左侧进入还是右侧进入
    setcdDuration(state, duration) {
      state.cdDuration = duration;
    },
    // 设置音乐是否是timeupdate
    setIsMusicTimeUpdate(state, bool) {
      state.isMusicTimeUpdate = bool;
    },
    // 设置音乐音量
    setVolume(state, num) {
      state.volume = num;
    },
    // 设置音乐歌词当前播放的索引
    setLyricIndex(state, index) {
      state.lyricIndex = index;
    },
    // 设置音乐结束后的播放类型
    setPlayType(state) {
      state.playType = state.playType === 3 ? 1 : ++state.playType;
    },

    // 播放音乐
    playMusic(state) {
      state.playing = true;
      state.audioelement.play();
    },
    // 暂停音乐
    pauseMusic(state) {
      state.playing = false;
      state.audioelement.pause();
    }
  },
  actions: {
    // 获取所有音乐列表数据
    set_MusicAllList({ commit }, obj) {
      commit('setMusicAllList', obj)
    },
    // 获取 audio DOM
    set_AudioElement({ commit }, ele) {
      commit('setAudioElement', ele)
    },
    // 切换播放暂停
    togglePlay({ commit, state }) {
      if (state.playing) {
        commit('pauseMusic');
      } else {
        commit('playMusic');
      }
    },
    // 播放下一首
    playNext({ state, commit }) {
      commit('setcdDuration', 'turntableLeft');

      state.currentIndex++;
      var length = state.musicList.length;
      if (state.currentIndex >= length) {
        state.currentIndex = 0;
      }
      state.audioelement.setAttribute('src', state.musicList[state.currentIndex].url);
      state.audioelement.load();
      commit('playMusic')
    },
    // 播放上一首
    playPrev({ state, commit }) {
      commit('setcdDuration', 'turntableRight');

      state.currentIndex--;
      var length = state.musicList.length;
      if (state.currentIndex < 0) {
        state.currentIndex = length - 1;
      }
      state.audioelement.setAttribute('src', state.musicList[state.currentIndex].url);
      state.audioelement.load();
      commit('playMusic')
    },

    // 设置音乐技术自动播放类型的歌曲
    palyEnded({state, commit}) {
      var type = state.playType;
      // 顺序播放
      if (type === 1) {
        state.currentIndex++;
        var len = state.musicList.length;
        if (state.currentIndex > len) {
          state.currentIndex = 0;
        }

        state.audioelement.setAttribute('src', state.musicList[state.currentIndex].url);
        state.audioelement.load();
        return commit('playMusic');
      }

      // 单曲循环
      if (type === 2) {
        state.audioelement.currentTime = 0;
        return commit('playMusic');
      }

      // 随机播放
      if (type === 3) {
        var len = state.musicList.length;
        state.currentIndex = Math.floor(Math.random() * len);

        state.audioelement.setAttribute('src', state.musicList[state.currentIndex].url);
        state.audioelement.load();
        return commit('playMusic');
      }
    },

    // 切换歌曲时的重置函数
    switchMusicInit({state, commit}) {
      commit('setLyricIndex', 0);
      commit({
        type: 'setCurrentTime',
        time: 0
      });
      state.audioelement.currentTime = 0;
    },

    // 播放当前的音乐
    playIndexMusic({state, commit}, index) {
      state.currentIndex = index;
      state.audioelement.setAttribute('src', state.musicList[state.currentIndex].url);
      state.audioelement.load();
      commit('playMusic');
    }
  }
}


