<template>
  <div id="app">
    <audio id="myaudio" ref="audio"
      @timeupdate="musicTimeUpdate"
      @canplay="musicCanPlay"
      @playing="musicPlaying"
      @ended="musicEnded"
      @waiting="musicWaiting"
      @pause="musicPause"
      @loadstart="musicLoadStart"
    ></audio>
    <!-- 头部header -->
    <v-header></v-header>
    <!-- 侧边栏列表 -->
    <side-bar :info="info"></side-bar>
    <!-- 主体内容 -->
    <div class="container">
      <transition :name="$store.state.routeDir" mode="out-in">
        <router-view></router-view>
      </transition>
    </div>
    <!-- 底部的菜单列表 -->
    <bottom-menu></bottom-menu>

    <!-- 歌单列表页 -->
    <song-sheet></song-sheet>
    <!-- 歌曲cd页 -->
    <music-detail></music-detail>
  </div>
</template>

<script>
import vHeader from '@/components/v-header/v-header'
import myMusic from '@/components/my-music/my-music'
import sideBar from '@/components/my-music/side-bar'
import bottomMenu from '@/components/bottom-menu/bottom-menu'
import songSheet from '@/components/song-sheet/song-sheet'
import musicDetail from '@/components/music-detail/music-detail'
import store from '@/store'

export default {
  name: 'App',
  data() {
    return {
      info: {}
    }
  },
  methods: {
    // 音乐播放结束事件
    musicEnded() {
      store.dispatch('switchMusicInit');
      store.commit('setIsMusicTimeUpdate', false);
      store.dispatch('palyEnded');
    },
    // 音乐播放时间更新事件
    musicTimeUpdate() {
      const audioDom = this.$refs.audio;
      const audioInfo = store.state.audioInfo;

      store.commit({
        type: 'setCurrentTime',
        time: audioDom.currentTime
      });

      store.commit('setIsMusicTimeUpdate', true);
      
    },
    // 可以播放事件
    musicCanPlay() {
      store.commit({
        type: 'setMusicLoadStart',
        isLoad: false
      });
      store.commit({
        type: 'setMusicDuration',
        duration: this.$refs.audio.duration
      });
    },
    // 音乐处于播放状态
    musicPlaying() {
      store.commit('playMusic');
    },
    // 音乐处于暂停状态
    musicPause() {
      store.commit('pauseMusic');
    },
    // 音乐处于 watting 状态
    musicWaiting() {
      store.commit('setIsMusicTimeUpdate', false);
      // console.log('加载中');
    },
    // 音乐加载
    musicLoadStart() {
      store.commit({
        type: 'setMusicLoadStart',
        isLoad: true
      });
      store.dispatch('switchMusicInit');
      store.commit('setIsMusicTimeUpdate', false);
    }
  },
  components: {
    'v-header':vHeader,
    'my-music':myMusic,
    'bottom-menu':bottomMenu,
    'side-bar':sideBar,
    'song-sheet':songSheet,
    'music-detail':musicDetail
  },
  created() {
    this.axios.get('https://rel-start.github.io/vue-music/static/data.json').then((res) => {
      // data.user的信息负值给info 在通过组件的数据传递给sideBar
      this.info = res.data.user;
      // 把所有的音乐数据给vuex的musicAllList
      store.dispatch('set_MusicAllList', res.data.music);
      // 给audio元素存在vuex 的state里面 方便日后调用
      store.dispatch('set_AudioElement', this.$refs.audio);
      // 把所有请求回来的数据给 allInfo
      store.dispatch('set_AllInfo', res.data);

      // 设置音乐的地址 初始化
      this.$refs.audio.setAttribute('src', store.getters.getCurrentMusic.url);
    }).catch(res => {
      console.log(res);
    })
  },
  mounted() {
    this.$refs.audio.volume = this.$store.state.audioInfo.volume;
  }
}
</script>

<style lang="scss">
@import './assets/css/reset.scss';
@import '../static/font-icon/style.css';


</style>


