import Vue from 'vue'
import Router from 'vue-router'

const myMusic = () => import(/* webpackChunkName: "group-foo" */'../components/my-music/my-music');
const songsheet = () => import(/* webpackChunkName: "group-foo" */'../components/song-sheet/song-sheet');
const findMusic = () => import(/* webpackChunkName: "group-foo" */'../components/find-music/find-music');
const finderecommend = () => import(/* webpackChunkName: "group-foo" */'../components/find-music/finderecommend');
const findsheet = () => import(/* webpackChunkName: "group-foo" */'../components/find-music/findsheet');
const radiostation = () => import(/* webpackChunkName: "group-foo" */'../components/find-music/radiostation');
const rankinglist = () => import(/* webpackChunkName: "group-foo" */'../components/find-music/rankinglist');
const community = () => import(/* webpackChunkName: "group-foo" */'../components/community/community');

Vue.use(Router)

export default new Router({
  hashbang: false,
  base: 'vue-music',
  mode: 'hash',
  routes: [
    {
      path: '/',
      redirect: '/mymusic'
    },
    {
      path: '/mymusic',
      name: 'mymusic',
      // component: (resolve) => require(['../components/my-music/my-music'], resolve),
      component: myMusic,
      meta: {
        index: 0,
        keepAlive: true
      }
    },
    {
      path: '/findmusic',
      // component: (resolve) => require(['../components/find-music/find-music'], resolve),
      component: findMusic,
      children: [
        {
          // findmusic第一页
          path: '',
          name:'findmusic',
          redirect: 'finderecommend',
        },
        {
          path: 'finderecommend',
          name: 'finderecommend',
          // component: (resolve) => require(['../components/find-music/finderecommend'], resolve),
          component: finderecommend,
          meta: {
            index: 1,
            keepAlive: true
          }
        },
        {
          path: 'findsheet',
          name: 'findsheet',
          // component: (resolve) => require(['../components/find-music/findsheet'], resolve),
          component:findsheet,
          meta: {
            index: 3,
            keepAlive: true
          }
        },
        {
          path: 'radiostation',
          name: 'radiostation',
          // component: (resolve) => require(['../components/find-music/radiostation'], resolve),
          component: radiostation,
          meta: {
            index: 4,
            keepAlive: true
          }
        },
        {
          path: 'rankinglist',
          name: 'rankinglist',
          // component: (resolve) => require(['../components/find-music/rankinglist'], resolve),
          component: rankinglist,
          meta: {
            index: 5,
            keepAlive: true
          }
        }
      ]
    },
    {
      path: '/community',
      name: 'community',
      // component: (resolve) => require(['../components/community/community'], resolve),
      component: community,
      meta: {
        index: 2,
        keepAlive: true
      }
    }
  ],
  linkActiveClass: 'active',
  linkExactActiveClass: 'active',
})



