<template>
  <div class="musicsheet_item">
    <div class="content" :style="{height: height+'px'}">
      <slot name="sheetimg"></slot>
      <div class="main">
        <div class="detail">
          <slot></slot>
        </div>
        <i v-show="!showVolume" class="edit icon-list-circle" @click.stop="showMenu(option.name)"></i>
        <i v-show="showVolume" class="isPlaying icon-volume-medium"></i>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["option", "showMenu", "height"],
  computed: {
    showVolume() {
      return this.$store.state.audioInfo.musicList === this.option.info
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";

.musicsheet_item {
  .content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &:active { background-color: $list_active; }
    .main {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex: 1;
      border-bottom: 1px solid $border_1px;
      height: 100%;
    }
    .detail {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .sheetimg {
      width: 50px;
      height: 50px;
      margin: 0 10px 0 6px;
    }
    .name {
      line-height: 26px;
    }
    .count {
      color: #9b9c9e;
      font-size: 12px;
    }
    .edit,
    .isPlaying {
      width: 36px;
      height: 36px;
      line-height: 36px;
      text-align: center;
      font-size: 16px;
      color: #999;
      margin-right: 5px;
      &:active {
        background: $list_active;
      }
    }
    .isPlaying {
      font-size: 14px;
      color: #c62f2f;
    }
  }
  &:last-child .content .main {
    border-bottom: 0;
  }
}
</style>



