export default {
  state: {
    isShow: false,
    detail: {
      title: '头部信息',
      content: []
    }
  },
  mutations: {
    showMenu (state, obj) {
      state.detail = obj === undefined ? state.detail : obj.amount;
      state.isShow = true;
    },
    hideMenu (state) {
      state.isShow = false;
    }
  }
}


