<template>
  <div class="rang_area">
    <slot name="sign">
      <dfn class="currentTime">{{ currentTime }}{{ showCd }}</dfn>
    </slot>
<div class="rang" ref="rang">
  <div :class="[type === 'progress' ? 'progress_dot' : 'volume_dot', {'active': canDrag}]" ref="dot" @touchstart="touchStart" @touchmove="touchMove" @touchend="touchEnd" :style="{left: progressDot}"></div>
  <div class="progress">
    <div :class="[type === 'progress' ? 'progress_play' : 'volume_play']" ref="play" :style="{width: progressPlayWidth}"></div>
  </div>
</div>
    <dfn class="totalTime" v-if="type === 'progress'">{{ musicDuration }}</dfn>
  </div>
  
</template>

<script>
var dotW = 8;

export default {
  data() {
    return {
      startX: 0,
      inintLeft: 0,
      progress: 0,
      progressMaxLeft: 0,
      playWidth: '0%',
      dotDuration: '0px',
      canDrag: false,
      volumeMaxLeft: 0,
    };
  },
  props: ['isShowMusicDetail', 'type', 'showCd'],
  computed: {
    volume() {
      return this.$store.state.audioInfo.volume;
    },
    currentTime() {
      return this.$store.getters.getCurrentTime;
    },
    musicDuration() {
      return this.$store.getters.getMusicDuration;
    },
    percent() {
      return this.$store.getters.getPercent;
    },
    progressDot() {
      if (this.type === 'progress') {
        if (this.$store.state.audioInfo.musicLoadStart) {
          return this.dotDuration = -dotW + 'px';
        }
        // musicTimeupdate 并且没有拖拽圆点，圆点跟随音乐运动
        else if (this.$store.state.audioInfo.isMusicTimeUpdate && !this.canDrag) {
          console.log(this.progressMaxLeft);
          return this.dotDuration = this.percent * this.progressMaxLeft - (dotW*(1-this.percent)) + 'px';
        } else {
          return this.dotDuration;
        }
      }
      if (this.type === 'volume') {
        return this.volume * this.volumeMaxLeft - (dotW*(1-this.volume)) + 'px';
      }
    },
    progressPlayWidth() {
      if (this.type === 'progress') {
        if (this.$store.state.audioInfo.musicLoadStart) {
          return this.playWidth = 0 + '%';
        }
        // musicTimeupdate 并且没有拖拽圆点，播放进度跟随音乐运动
        else if (this.$store.state.audioInfo.isMusicTimeUpdate && !this.canDrag) {
          return this.playWidth = this.percent * 100 + '%';
        } else {
          return this.playWidth;
        }
      }

      if (this.type === 'volume') {
        return this.volume * 100 + '%';
      }
    }
  },
  watch: {
    isShowMusicDetail(newVal) {
      if (newVal) {
        var { rang, dot } = this.$refs;
        console.log(rang.offsetWidth, dot.offsetWidth);
        this.progressMaxLeft = rang.offsetWidth - dot.offsetWidth + dotW;
      }
    },
    showCd(newVal) {
      if (!newVal) {
        var { rang, dot } = this.$refs;
        this.volumeMaxLeft = rang.offsetWidth - dot.offsetWidth + dotW;
      }
    }
  },
  methods: {
    touchStart(e) {
      this.canDrag = true;
      this.startX = e.touches[0].clientX;
      this.inintLeft = this.$refs.dot.offsetLeft;
    },
    touchMove(e) {
      if (this.canDrag) {
        var { rang, dot, play } = this.$refs;
        var dx = e.touches[0].clientX - this.startX + this.inintLeft;

        if (this.type === 'progress') {
          var maxLeft = this.progressMaxLeft;
          // 边界判断
          if (dx < -dotW) dx = -dotW;
          if (dx > maxLeft) dx = maxLeft;
          // 播放进度比例
          this.progress = (dx+dotW) / (maxLeft+dotW);

          dot.style.left = dx + 'px';
          play.style.width = this.progress * 100 + '%';
        }

        if (this.type === 'volume') {
          var maxLeft = this.volumeMaxLeft;
          // 边界判断
          if (dx < -dotW) dx = -dotW;
          if (dx > maxLeft) dx = maxLeft;
          // 音乐音量
          this.$store.commit('setVolume',(dx+dotW) / (maxLeft+dotW));
          
          dot.style.left = dx + 'px';
          play.style.width = this.volume * 100 + '%';
          this.$store.state.audioInfo.audioelement.volume = this.volume;
        }
      }
    },
    touchEnd() {
      var audioInfo = this.$store.state.audioInfo;
      if (this.type === 'progress') {
        this.$store.commit({
          type: "setCurrentTime",
          time: this.progress * audioInfo.musicDuration
        });
        audioInfo.audioelement.currentTime =
        this.progress * audioInfo.musicDuration;
      }
      if (this.type === 'volume') {
        audioInfo.audioelement.volume = this.volume;
      }
      this.canDrag = false;
    }
  },
  mounted() {
    
  }
};
</script>

<style lang="scss" scoped>
// @M 播放进度条
.rang_area {
  width: 90%;
  margin: 0 auto;
  display: flex;
  line-height: 24px;
  .currentTime {
    margin-right: 10px;
    vertical-align: middle;
  }
  .rang {
    flex: 1;
    position: relative;
    display: flex;
    align-items: center;
    height: 24px;
    .progress {
      display: inline-block;
      width: 100%;
      height: 2px;
      background-color: rgba(255, 255, 255, 0.3);
      .progress_play {
        width: 0%;
        height: 2px;
        background-color: #c50600;
      }
      .volume_play {
        width: 0%;
        height: 2px;
        background-color: rgba(255,255,255,.8);
      }
    }
    .progress_dot {
      position: absolute;
      top: calc(50% - 15px);
      left: 0;
      width: 14px;
      height: 14px;
      border: 8px solid transparent;
      box-sizing: content-box;
      background: -webkit-radial-gradient(#c50600 1px, #fff 2px);
      background-clip: content-box;
      border-radius: 50%;
      &.active {
        border-color: rgba(255,255,255,.2);
      }
    }
    .volume_dot {
      position: absolute;
      top: calc(50% - 14px);
      left: 0;
      width: 12px;
      height: 12px;
      border: 8px solid transparent;
      box-sizing: content-box;
      background: #fff;
      box-shadow: 0 0 0 5px transparent;
      background-clip: content-box;
      border-radius: 50%;
      &.active {
        border-color: rgba(255,255,255,.2);
      }
    }
  }
  .totalTime {
    margin-left: 10px;
    vertical-align: middle;
  }
}
</style>



