<template>
  <div :class="['sideitem',{bt5:haveBorT}]">
    <div class="sign">
      <i :class="['ico', iconinfo]"></i>
    </div>
    <div class="info">
      <span class="title">{{ title }}</span>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: ['iconinfo','title','haveBorT']
}
</script>

<style lang="scss" scoped>
@import "../../assets/css/global.scss";

.sideitem {
  box-sizing: content-box;
  height: 42px;
  line-height: 42px;
  &:active { background-color: $list_active; }
  &.bt5 { border-top: 6px solid #f2f3f6; }  
  .sign {
    float: left;
    width: 40px;
    height: 42px;
    text-align: center;
    .ico {
      vertical-align: middle;
      font-size: 18px;
      color: #919191;
    }
  }
  .info {
    width: 260px;
    height: 42px;
    float: left;
    .title {
      vertical-align: middle;
      color: #000;
    }
    .other {
      font-size: 12px;
      color: #999;
      float: right;
    }
  }
}
</style>



