<template>
  <div :class="['typelist', {lastBorNone:lastBorNone}]">
    <div class="typelist_icon">
      <i :class="iconinfo"></i>
    </div>
    <div class="typelist_detail">
      <span class="name">{{ name }}</span>
      <span class="count" v-show="count !== 'none'">({{ count }})</span>
      <i v-if="playing" class="isPlaying icon-volume-medium"></i>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    iconinfo: String,
    count: [Number,String],
    name: String,
    playing: Boolean,
    lastBorNone: {
      type: Boolean,
      default: false
    }
  }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/global.scss';

.typelist {
  display: flex;
  height: 50px;
  .typelist_icon { display: flex; justify-content: center; align-items: center; width: 50px;
    i { font-size: 20px; color: $primarycolor; }
  }
  .typelist_detail { line-height: 50px; flex: 1;
    border-bottom: 1px solid $border_1px;
    .count { color: #aaa; font-size: 10px; margin-left: 6px; }
    .isPlaying { color: $primarycolor; float: right; width: 36px; margin: 7px 5px 0 0; line-height: 36px; height: 36px; text-align: center;
      &:active { background-color: $list_active }
    }
  }
}

.lastBorNone:last-child .typelist_detail { border-bottom: 0; }
</style>



