<template>
  <div class="musicsheet">
    <div class="title" @click="toggleSheets">
      <div class="info">
        <i class="toggle icon-down" ref="toggleicon"></i>
        <span class="name">{{ data_item.name }}</span>
        <span class="count">({{ data_item.count }})</span>
      </div>
        <i class="setting icon-setting" @click.stop="showSheetMenu(data_item.name)"></i>
    </div>

    <musicSheetItem v-show="showSheets" v-for="(option, index) in data_item.detail" :key="index" @click.native="showSongSheet(option)" :option="option" :show-menu="showMenu" :height="60">
      <img class="sheetimg" v-lazy="option.info[0].img_url" slot="sheetimg">

      <template>
        <p class="name">{{ option.name }}</p>
        <p class="count">{{ option.count }}首</p>
      </template>
    </musicSheetItem>
  </div>
</template>

<script>
import musicSheetItem from './music-sheet-item'

export default {
  props: {
    item: {
      type: Object
    }
  },
  data() {
    return {
      showSheets: true,
      data_item: {}
    }
  },
  components: {
    musicSheetItem
  },
  methods: {
    toggleSheets() {
      this.$refs.toggleicon.style.transform = this.showSheets ? 'rotate(-90deg)' : 'rotate(0)'
      this.showSheets = !this.showSheets;
    },
    showMenu(name) {
      this.$store.commit({
        type: 'showMenu',
        amount: {
          height: 205,
          title: `歌单:${name}`,
          content: [
            {
              name: '分享',
              iconinfo: 'icon-share',
              count: 'none',
              bgcolor: '#fff'
            },
            {
              name: '编辑',
              iconinfo: 'icon-edit',
              count: 'none',
              bgcolor: '#fff'
            },
            {
              name: '删除',
              iconinfo: 'icon-delete',
              count: 'none',
              bgcolor: '#fff'
            }
          ]
        }
      });
    },
    showSheetMenu(name) {
      this.$store.commit({
        type: 'showMenu',
        amount: {
          height: 105,
          title: name,
          content: [
            {
              name: '删除',
              iconinfo: 'icon-delete',
              count: 'none',
              bgcolor: '#fff'
            }
          ]
        }
      });
    },
    // 显示歌单列表
    showSongSheet(data) {
      const {commit} = this.$store;
      // this.$router.push({ name: 'songsheet' });
      commit({
        type: 'setIsShowSongSheet',
        isShow: true
      });

      commit({
        type: 'setMusicSheetList',
        data: data
      });
    }
  },
  mounted() {
    this.data_item = this.item;
  }
}
</script>

<style lang="scss" scoped>
.musicsheet {
  .title { display: flex; justify-content: space-between; align-items: center; height: 30px; background: #e7e9e8; font-weight: 400;
    .info { display: flex; align-items: center; }
    .toggle { margin: 0 12px; transition: transform .5s; }
    .name,.count { font-size: 12px; }
    .setting { font-size: 20px; width: 30px;height: 30px; text-align: center; line-height: 30px; margin-right: 8px; }
  }

}
</style>



