<template>
  <div class="mymusic">
    <div>
      <type-list :key="index" v-for="(titem,index) in typeListData" v-bind="titem" :lastBorNone="true"></type-list>
    </div>

    <music-sheet v-for="(item, index) in items" :item="item" :key="index"></music-sheet>
  <router-view></router-view>
    <!-- 底部显示的菜单列表 -->
    <menu-list></menu-list>
  </div>
</template>

<script>
import typeList from '@/components/my-music/type-list'
import musicSheet from '@/components/my-music/music-sheet'
import menuList from '@/components/my-music/menu-list'

export default {
  computed: {
    items() {
      return this.$store.state.audioInfo.musicAllList.sheets
    }
  },
  data() {
    return {
      typeListData: [
        { name: '我的音乐', iconinfo: 'icon-music', count: 28, playing: true },
        { name: '最近播放', iconinfo: 'icon-zuijinplay', count: 18 },
        { name: '我的电台', iconinfo: 'icon-diantai', count: 1 },
        { name: '我的收藏', iconinfo: 'icon-collect', count: 3 },
      ]
    }
  },
  components: {
    'type-list': typeList,
    'music-sheet': musicSheet,
    'menu-list': menuList
  }
}
</script>

<style scoped>
.mymusic {
  position: absolute;
  top: 50px;
  bottom: 50px;
  left: 0;
  right: 0;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  background-color: #f3f4f6;
}
</style>




