import Vue from 'vue'
import Vuex from 'vuex'
import AudioInfo from './modules/audio.js'
import menuList from './modules/menu-list.js'
import sidebar from './modules/sidebar.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    routeDir: '',
    // 所有数据
    allInfo: {}
  },
  mutations: {
    setRouteDir (state, bool) {
      state.routeDir = bool ? 'right' : 'left';
    },
    setAllInfo (state, obj) {
      state.allInfo = obj;
    }
  },
  getters: {
    // swiper 轮播图的数据
    recommendImageList: state => {
      return state.allInfo.findmusic ? state.allInfo.findmusic.reconmmend.recommendImageList : state.allInfo;
    },
    // 推荐歌单的数据
    recommendMinSheets: state => {
      return state.allInfo.findmusic ? state.allInfo.findmusic.reconmmend.recommendMinSheets : state.allInfo;
    }
  },
  actions: {
    set_AllInfo ({ commit }, obj) {
      commit('setAllInfo', obj);
    }
  },
  modules: {
    audioInfo: AudioInfo,
    menuList: menuList,
    sidebar: sidebar,

  }
})


